import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Imgcheck from '../components/imgcheck'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'imgcheck',
      component: Imgcheck
    }
  ]
})



